html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.leaflet-container {
  height: 100%;
  width: 100%;
}

.leaflet-popup-content-wrapper, .leaflet-popup-tip {
  color: #fff;
  background-color: #797676;
}

.app__container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-size: cover;
  font-family: "Open Sans", serif; }

.main__container {
  position: relative; }

.arrow {
  width: 2.5rem;
  margin-left: 1rem; }

.loader__container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 400; }

.loader__spinner {
  width: 16rem;
  height: 16rem;
  margin: 100px auto;
  background-color: #c8c8c8;
  border-radius: 100%;
  animation: sk-scaleout 1.0s infinite ease-in-out; }

@keyframes sk-scaleout {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1);
    opacity: 0; } }

.map {
  width: 100%; }

.menu__container {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #797676;
  color: #fff;
  padding: 1rem 2rem;
  z-index: 500;
  box-shadow: -2px 2px 2px #000; }

.menu__header {
  font-size: 1.5rem;
  margin-top: 0; }

.menu__content {
  margin-top: 1rem;
  display: flex;
  flex-direction: column; }

.header {
  font-size: 1.8rem;
  text-align: center;
  margin: 0 auto 2rem; }

.checkbox__label {
  border-radius: 50%;
  cursor: pointer;
  display: block;
  padding-left: 3.5rem;
  position: relative;
  top: 0.25rem;
  font-size: 1.4rem;
  margin-bottom: 2.5rem; }
  .checkbox__label:hover .checkbox__input ~ .checkbox__indicator {
    border-color: black; }

.checkbox__input {
  opacity: 0;
  position: absolute;
  z-index: -1; }
  .checkbox__input:focus ~ .checkbox__indicator {
    border-color: black; }
  .checkbox__input:checked ~ .checkbox__indicator::after {
    display: block; }
  .checkbox__input:disabled ~ .checkbox__indicator {
    opacity: 0.6;
    pointer-events: none; }

.checkbox__indicator {
  background-color: white;
  border: 1px solid #999;
  border-radius: 50%;
  height: 24px;
  left: 0;
  position: absolute;
  top: 0;
  width: 24px; }
  .checkbox__indicator::after {
    background-color: mediumseagreen;
    border-radius: 50%;
    content: "";
    display: none;
    height: 16px;
    left: 4px;
    position: absolute;
    top: 4px;
    width: 16px; }

.info-tab__container {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  padding: 1rem;
  background-color: #797676;
  border: 1px solid transparent;
  z-index: 400;
  cursor: pointer;
  transition: 0.3s; }
  .info-tab__container:hover {
    background-color: #fff;
    border: 1px solid #797676; }
    .info-tab__container:hover .info-tab__author {
      color: #797676; }

.info-tab__author {
  color: #fff;
  font-size: 1.2rem;
  text-decoration: none; }

